<template>
  <div class="home_box">
    <!-- header头部 -->
    <header class="header_box" :class="{ active: !isTop }">
      <div class="left_box">
        <van-icon name="wap-nav" />
      </div>
      <div class="middle_box">
        <span class="app-name">新蜂商城</span>
        <router-link to="/search" class="search-title">
          山河无恙，人间皆安
        </router-link>
      </div>
      <div class="right_box" @click="login">
        <span v-if="!islogin">登录</span>
        <van-icon name="manager-o" v-if="islogin" />
      </div>
    </header>

    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="rgb(27,174,174)">
      <van-swipe-item v-for="(item) in carousels" :key="item.carouselUrl">
        <img :src="item.carouselUrl" alt="">
      </van-swipe-item>
    </van-swipe>

    <!-- 标签导航 -->
    <van-grid square :column-num="5" :border="false">
      <van-grid-item icon="http://s.weituibao.com/1583585285461/cs.png" text="新蜂超市" />
      <van-grid-item icon="http://s.weituibao.com/1583585285468/fs.png" text="新蜂服饰" />
      <van-grid-item icon="http://s.weituibao.com/1583585285470/qq.png" text="全球购" />
      <van-grid-item icon="http://s.weituibao.com/1583585285472/sx.png" text="新蜂生鲜" />
      <van-grid-item icon="//s.weituibao.com/1583585285467/dj.png" text="新蜂到家" />
      <van-grid-item icon="//s.weituibao.com/1583585285465/cz.png" text="充值缴费" />
      <van-grid-item icon="//s.weituibao.com/1583585285469/pt.png" text="9.9元拼" />
      <van-grid-item icon="//s.weituibao.com/1583585285468/juan.png" text="领券" />
      <van-grid-item icon="//s.weituibao.com/1583585285471/sq.png" text="省钱" />
      <van-grid-item icon="//s.weituibao.com/1583585285470/qb.png" text="全部" />
    </van-grid>

    <!-- 新品上线 -->
    <div class="newonline">
      <p>新品上线</p>
      <Goods :dataItem='newGoodses'>
      </Goods>
    </div>
    <!-- 热门商品 -->
    <div class="newonline">
      <p>热门商品</p>
      <Goods :dataItem='hotGoodses'>
      </Goods>
    </div>
    <!--  最新推荐-->
    <div class="newonline">
      <p>最新推荐</p>
      <Goods :dataItem='recommendGoodses'>
      </Goods>
    </div>
  </div>
</template>

<script>
// 导入首页接口
import { getIndexInfo } from "@/api/index"
// 导入Good组件
import Goods from "@/components/Goods.vue"
export default {
  name: "home",
  props: [],
  components: {
    Goods,
  },
  created() {  
    // 根据令牌判断是否登录
    this.islogin=localStorage.getItem('xftoken');
    // 定义函数调用首页接口
    this.getData();
  },
  data() {
    return {
      isTop: true,  //true表示header没有active这个class类
      carousels: [],//轮播图数据
      hotGoodses: [],//热销商品的数据
      newGoodses: [],//新品数据
      recommendGoodses: [],//推荐数据
      islogin:'',
    }
  },
  methods: {
    //  动态给header添加active类
    changeTop() {
      // 获取页面卷动的高度
      let t = document.documentElement.scrollTop || document.body.scrollTop
      if (t > 50) {
        // 添加active类
        this.isTop = false
      } else {
        this.isTop = true
      }
    },

    //  
    getData() {
      // 调用首页借口
      getIndexInfo().then(data => {
        if (data.resultCode === 200) {
           // console.log(data);
        this.carousels = data.data.carousels;
        this.hotGoodses = data.data.hotGoodses;
        this.newGoodses = data.data.newGoodses;
        this.recommendGoodses = data.data.recommendGoodses;}
      }).catch(err => {
      })
    },
    //  点击头部的右上角登录
    login() {
      this.$router.push('/login')
    }
  },
  mounted() {
    // 触发页面卷动事件
    window.addEventListener("scroll", this.changeTop);
  },
  beforeDestroy() {
    // 注销页面卷动事件
    window.removeEventListener("scroll", this.changeTop);
  },
}
</script>

<style lang='less' scoped>



// header头部样式
.home_box {
  .header_box {
    position: fixed;
    width: 100%;
    height: 50px;
    display: flex;
    z-index: 200;
    top: 0;
    left: 0;

    .left_box {
      width: 50px;
      flex: none;
      background: rgba(0, 0, 0, 0);

      /deep/.van-icon-wap-nav {
        margin: 50%;
        transform: translate(-50%, -50%);
        color: #1baeae;
        font-size: 16px;
      }
    }

    .right_box {
      width: 50px;
      flex: none;
      background: rgba(0, 0, 0, 0);
      text-align: center;
      line-height: 50px;
      font-size: 16px;
      color: #1baeae;
      padding: 0 7px;
    }

    .middle_box {
      display: flex;
      align-items: center;
      flex: auto;
      background: hsla(0, 0%, 100%, .7);
      border-radius: 20px;
      height: 30px;
      line-height: 30px;
      margin-top: 10px;

      .app-name {
        box-sizing: border-box;
        height: 20px;
        line-height: 20px;
        font-size: 20px;
        font-weight: 700;
        color: #1baeae;
        border-right: 0.1px solid #666;
        padding: 0 10px;
      }

      .search-title {
         font-size: 12px;
         color: #666;
         box-sizing: border-box;
         padding-left: 20px;
      }

    }

    &.active {
      background: #1baeae;


      .right_box {
        color: white;
      }

    }
  }

  // 轮播图样式
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    text-align: center;

    img {
      width: 100%;
      height: auto;
    }
  }

  padding-bottom: 100px;
}

p {
  background: #f9f9f9;
  text-align: center;
  color: #1baeae;
  height: 52px;
  line-height: 52px;
  font-size: .42667rem;
  font-weight: 500;
}

.newonline {
  /deep/.van-grid-item__text {
    font-size: 16px;
    width: 90%;
    color: #222333;
    text-align: center;
  }

  /deep/.van-icon__image {
    width: 88px;
    height: 88px;
  }
}


